.footer__copyright {
  font-family: "Source Sans Pro";
  font-size: 9px;
  line-height: 12px;
  color: #1a1a1a;
  max-width: 180px;
}
.footer {
  font-family: "Source Sans Pro";
  bottom: 0;
  width: 100%;
  background-image: url('../../assets/img/Fon\ header.jpg');
  padding-top: 40px;
  padding-bottom: 30px;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  gap: 50px;
}

.footer1 {
  font-family: "Source Sans Pro";
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

@media (max-width: 768px) {
  .footer__logo {
    display: none;
  }
  .footer__copyright {
    display: none;
  }
  .menu-bottom {
    display: none;
  }
  .footer__wrapper {
    text-align: center;
    justify-content: center;
  }
}
